import React, { Component } from "react";
import { Grid, Row, Col, Table, Button } from "react-bootstrap";

import Card from "components/Card/Card.jsx";
import { thArray, tdArray } from "variables/Variables.jsx";

class TableList extends Component {
  render() {
    return (
      <div className="content">
        <Grid fluid>
          <Row>
            <Col md={12}>
              <Card
                title="List of forum"
                category="Get all forum "
                ctTableFullWidth
                ctTableResponsive
                content={
                  <Table striped hover>
                    <thead>
                      <tr>
                        <td>
                          <Button variant="primary" className="form_button">All Forum</Button>
                        </td>
                        <td >                          
                         
                            <input type="text" placeholder="Search forum" className="mr-sm-2" />
                            <Button variant="primary" className="search_button"><i class="fa fa-search" aria-hidden="true"></i></Button>
                          
                        </td>
                      </tr>
                      <tr>
                        {thArray.map((prop, key) => {
                          console.log('props :::::', prop);

                          return <th key={key}>{prop}</th>;


                        })}
                      </tr>
                    </thead>
                    <tbody>
                      {tdArray.map((prop, key) => {
                        return (
                          <tr key={key}>
                            {prop.map((prop, key) => {
                              return <td key={key}>{prop}</td>;
                            })}

                            <td>
                              <Button variant="primary" className="form_button">Details</Button>
                            </td>
                            <td>
                              <Button variant="primary" className="form_button"><i class="fa fa-trash-o" aria-hidden="true"></i></Button>
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </Table>
                }
              />
            </Col>


          </Row>
        </Grid>
      </div>
    );
  }
}

export default TableList;
